export default function Stock() {
  const stock = [
    { id: 1, product: "Diesel", quantity: "5,000L", status: "Delivered" },
    { id: 2, product: "Petrol", quantity: "3,000L", status: "Pending" },
  ]

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Stock Updates</h1>
      <table className="w-full bg-white rounded-xl shadow-md">
        <thead className="bg-teal-600 text-white">
          <tr>
            <th className="p-3 text-left">Product</th>
            <th className="p-3 text-left">Quantity</th>
            <th className="p-3 text-left">Status</th>
          </tr>
        </thead>
        <tbody>
          {stock.map((s) => (
            <tr key={s.id} className="border-t hover:bg-gray-100">
              <td className="p-3">{s.product}</td>
              <td className="p-3">{s.quantity}</td>
              <td className="p-3">{s.status}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
